.header {
   max-width: 1440px;
   width: 100%;
   padding: 16px 75px;
   position: fixed;
   top: 0;
   background-color: var(--color-bg-primary-header);
   backdrop-filter: blur(10px);
   display: flex;
   flex-direction: column;
   align-items: center;
   justify-content: space-between;
   z-index: 7;
}

.menuIcon {
   fill: var(--color-green);
}

.headerContainer {
   width: 100%;
   position: relative;
}

.containerLinks {
   width: 400px;
   height: 400px;
   background-color: white;
   padding: 25px;
}

.menuLink {
   margin-bottom: 15px;
   cursor: pointer;
}

.modalMenu {
   position: absolute;
   top: 85px;
   left: calc((100vw - 1440px) / 2);
}

.logo {
   text-align: center;
   line-height: 22px;
   margin: 0 4px 0 7px;
}

.delivery_text:last-child {
   margin-left: 20px;
}

.button {
   margin-top: 10px;
}
